@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@100;200;300;400;500;600;700;800;900&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'League Spartan', 'Poppins';
}

.App {
  height: 120vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(44, 49, 63);

  .main-container {
    max-width: 430px;
    border-radius: 10px;
    background-color: rgb(17, 17, 35);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .image-container {
      display: flex;
      justify-content: center;

      img {
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        width: 430px;
        height: 410px;
        object-fit: cover;
      }
    }

    .text-container {
      width: 370px;
      padding-top: 20px;
      padding-bottom: 20px;
      color: #fff;

      main {
        .identity-container {
          display: flex;
          justify-content: center;
          flex-direction: column;
          margin-bottom: 15px;
          align-items: center;

          .name {
            margin-bottom: -5px;
          }

          .job {
            font-weight: 600;
            margin-bottom: 5px;
            color: rgb(133, 154, 173);
          }

          .website {
            font-weight: 600;
          }
        }

        .btn-container-wrapper {
          display: flex;
          justify-content: center;
          width: 340px;

          .btn-container {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 340px;

            button {
              display: flex;
              align-items: center;
              justify-content: center;
              border: none;
              border-radius: 5px;
              padding: 6px 25px 6px 25px;
              width: 154px;

              .icon-container {
                margin-right: 10px;

                i {
                  font-size: 1.4rem;
                }
              }

              h2 {
                font-weight: 300;
              }
            }

            .email-btn {
              background-color: #fff;
            }

            .linkedIn-btn {
              background-color: rgb(47, 161, 255);
              color: #fff;
            }
          }
        }

        .text-container {
          .about-container{
            margin-top: 15px;
            margin-bottom: 15px;
          }
          h2 {
            color: #fff;
          }

          p {
            color: rgb(236, 236, 236);
          }
        }
      }

      footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 10px;
        margin-bottom: 10px;

        i {
          font-size: 2rem;
        }
      }
    }
  }
}