  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.App .main-container .image-container {
  display: flex;
  justify-content: center;
}
.App .main-container .image-container img {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  width: 430px;
  height: 410px;
  -o-object-fit: cover;
     object-fit: cover;
}
.App .main-container .text-container {
  width: 370px;
  padding-top: 20px;
  padding-bottom: 20px;
  color: #fff;
}
.App .main-container .text-container main .identity-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-bottom: 15px;
  align-items: center;
}
.App .main-container .text-container main .identity-container .name {
  margin-bottom: -5px;
}
.App .main-container .text-container main .identity-container .job {
  font-weight: 600;
  margin-bottom: 5px;
  color: rgb(133, 154, 173);
}
.App .main-container .text-container main .identity-container .website {
  font-weight: 600;
}
.App .main-container .text-container main .btn-container-wrapper {
